.icon {
    margin-bottom: 40px;
    display: flex;
    min-width: 80px;
    width: 80px;
    height: 80px;
    background: url('/public/img/dotted-bg.svg');
    align-items: center;
    justify-content: center;
}

.icon img {
    width: 38px;
    height: 38px;
}

.text p:nth-child(1) {
    font-size: 24px;
    font-weight: 600;
    line-height: 22px;
    letter-spacing: -0.03em;
    margin-bottom: 6px;
}

.text p:nth-child(2) {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0px;
    color: #000000B2;

}

@media (max-width: 768px) {
    .text p:nth-child(1) {
        font-size: 22px;
    }
}

@media (max-width: 480px) {
    .wrapper {
        display: flex;
        column-gap: 12px;
        align-items: center;
    }

    .icon {
        background-repeat: no-repeat;
        margin-bottom: 0;
    }

    .text p:nth-child(2) {
        font-size: 15px;
    }
}